.loader {
  width: 100%;
  height: 4px;
  position: absolute;
  left: 0;
  overflow: hidden;
}

.loader:after {
  content: "";
  background: var(--blue);
  box-sizing: border-box;
  width: 192px;
  height: 4px;
  animation: 2s linear infinite animloader;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes animloader {
  0% {
    left: 0;
    transform: translateX(-100%);
  }

  100% {
    left: 100%;
    transform: translateX(0%);
  }
}

.scrollup {
  background-color: var(--blue);
  z-index: 0;
  visibility: hidden;
  opacity: .5;
  box-shadow: 0px 0px 0px 5px var(--blue-900);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 54px;
  height: 54px;
  padding: 5px;
  transition: all .2s linear;
  display: flex;
  position: fixed;
  bottom: -20%;
  right: 16px;
}

.scrollup:hover {
  opacity: 1;
  transform: translate3d(0, -10px, 0);
}

.scrollup__icon {
  width: 24px;
  height: 24px;
}

.scrollup-show {
  visibility: visible;
  bottom: 16px;
}

